html, body {
    margin: 0;
    padding: 0;
    font-family: 'Raleway', sans-serif;
    letter-spacing: 0.1rem;
}

/* .gradient {
    background: -webkit-linear-gradient(45deg, #0238fb, #ef8c3e 80%);
} */

.container {
    margin: 0;
    max-width: 100%;
}

.col {
    padding: 0;
}

.navbar{
    font-family: 'Raleway', sans-serif;
    padding: 0;
    position: fixed;
    top: 0;
    z-index: 999;
    width: 100%;
}

.navbar-box{
    background-color: white !important;
}

.navbar-name{
    font-weight: bold;
    letter-spacing: 0.8rem;
    text-transform: uppercase;
    font-size: 1.75rem;
    cursor: pointer;
    padding-left: 1%;
    color: rgb(47, 47, 47) !important;
}

/* @media (min-width: 768px){
    .pr-md-5, .px-md-5 {
        padding-right: 0 !important;
    }
} */

#responsive-navbar-nav {
    text-align: center;
}

.nav-link {
    display: inline-block;
    color: rgba(0,0,0,.5);
    text-decoration: none;
}

.nav-link::after {
    content: '';
    display: block;
    width: 0;
    height: 1px;
    background: rgba(0,0,0,.5);
    transition: width .3s;
}

.nav-link:hover::after {
    width: 100%;
    transition: width .3s;
}

img {
    object-fit: cover;
    height: 100vh;
}
.contact {
    background-image: url(/jocelyn-yoga/static/media/DSC04275.09773680.jpg);
    background-size: cover;
    height: 500px;
    background-position: 0% 30%;
}

.contact-form {
    width: 30%;
    color: white;
    margin: 0;
    position: absolute;
    top: 50%;
    left: 2%;
    -webkit-transform: translate(0%, -50%);
            transform: translate(0%, -50%);
}

.form-box {
    width: 100%;
}
